<template>
    <div><slot></slot></div>
</template>


<style lang="scss" rel="stylesheet/scss">
    $webpack-build: true;
    $font-path: "../assets/fonts";
    $fa-font-path: $font-path + '/FontAwesome';
    @import "../../core/styles/codex.page.welcome";

    @mixin carousel-slide($slideName, $slideExt) {
        $prefix: '/assets/img/slides/slide-';
        background-image: url($prefix + $slideName + '-576.' + $slideExt);
        @include media-breakpoint-up(sm) {
            background-image: url($prefix + $slideName + '-768.' + $slideExt);
        }
        @include media-breakpoint-up(md) {
            background-image: url($prefix + $slideName + '-922.' + $slideExt);
        }
        @include media-breakpoint-up(lg) {
            background-image: url($prefix + $slideName + '-1200.' + $slideExt);
        }
        @include media-breakpoint-up(xl) {
            background-image: url($prefix + $slideName + '-2000.' + $slideExt);
        }
    }

    .carousel.carousel-codex .carousel-item {
        &.slide-html {
        }
        &.slide {
            &-html { @include carousel-slide('html', 'png') }
            &-typewriter { @include carousel-slide('typewriter', 'jpg') }
        }
    }

    #welcome-carousel {

        // dimensions
        // options
        /* Carousel base class */
        .carousel-inner {
            height: 100%;
        }
        .carousel-item {
            background-size: cover;
            //background: rgba(0, 0, 0, 0) no-repeat center center;
            width: 100%;
            height: 100%;
            opacity: 1;
        }
        .carousel-caption {
            position: absolute;
            //@include center-width(70);
            //top: 200px;
            //margin: 0 auto;
            left: 10%;
            right: 10%;
            bottom: 20%;
            z-index: 10;
            width: 80%;
            //max-width: 580px;
            //border: 1px solid $carousel-caption-border-color;
            padding: 20px;
        }
        .carousel-caption, .carousel-caption p {
            font-size: $carousel-caption-font-size;
        }

        // with this
        .carousel-control {
            &.bottom {
                position: relative;
                bottom: 90px;
                display: block;
                top: initial;
                text-align: center;
                color: white;
                font-size: 70px;
                z-index: 2;
                margin: 0 auto;
                //color: rgba(white, 0.6);
                &:hover {
                    //color: rgba(white, 0.9);
                }
            }

            .icon-bottom {
                @include fa-icon();
                position: absolute;
                z-index: 5;
                display: inline-block;
                width: $carousel-icon-width;
                height: $carousel-icon-width;
                margin-top: -($carousel-icon-width / 2);
                font-family: serif;
                line-height: 1;
                &:before {
                    content: $fa-var-chevron-down;
                }
            }
        }

        @include media-breakpoint-up(xl) {
            .carousel-caption {
                left: 20%;
                right: 20%;
                width: 60%;
            }
        }
    }

    .carousel.carousel-codex-feature {

        .carousel-item {
            background-position: top left;
        }

        .carousel-caption {
            left: 0;
            right: 0;
            width: 100%;
            bottom: 0;
            background: rgba(0, 0, 0, 0.7);
            font-size: 1rem;
            padding: 5px;
            text-align: left;
            p {
                font-size: 1rem;
                margin: 0;
            }
        }
        .carousel-indicators {
            bottom: -45px;
            li {
                border-color: material-color(blue-grey, '500');
                background-color: material-color(blue-grey, '900');
            }
            li.active {
                //border-color: material-color(blue-grey, '200');
                background-color: $color-codex;
            }
            li, li.active {
                border-radius: 20%;
                width: 20px;
                height: 20px;
                margin: 1px 5px;
            }
        }
    }

</style>

<script>
    export default {}
</script>


