<template>
  <span ref="trigger">
    <slot></slot>
    <div ref="popover" v-show="visible" :class="['popover',placement, popoverClass]" :transition="effect">
      <div class="arrow"></div>
      <h3 class="popover-title" v-if="title"><slot name="title">{{title}}</slot></h3>
      <div class="popover-content"><slot name="content"><span v-html="content"></span></slot></div>
    </div>
  </span>
</template>

<script>
    import PopoverMixin from '../mixins/popover'

    export default {
        name: 'c-popover',
        mixins: [PopoverMixin],
        props: {
            trigger: {type: String, default: 'click'},
            popoverClass: {type: String, default: '' }
        }
    }
</script>



