<template>
  <span ref="trigger">
    <slot></slot>
    <transition :name="effect">
      <div ref="popover" v-show="visible" style="display:block;" :class="['tooltip',placement]">
        <div class="tooltip-arrow"></div>
        <div class="tooltip-inner" v-html="content"><slot name="content"></slot></div>
      </div>
    </transition>
  </span>
</template>

<script>
    import PopoverMixin from '../mixins/popover'

    export default {
        mixins: [PopoverMixin],
        props: {
            effect: {type: String, default: 'scale'},
            trigger: {type: String, default: 'hover'}
        }
    }
</script>

<style>
</style>


