<template>
    <div>
        <drag-handle
                @move="move"
                @max="open(false)"
                @aborted="hide"
                :disabled="opened || isFixed"
                :max-right="right ? null : width"
                :max-left="right ? width : null"
                :z-index="overlayZIndex"
                :style="{width: '20px',left:right ? null : '0',right:right ? '0' : null}">

        </drag-handle>
        <drag-handle
                @move="move"
                @max="close(false)"
                @aborted="show"
                :disabled="!opened || isFixed"
                :max-right="right ? width : null"
                :max-left="right ? null : width"
                :offset="right ? -width : width"
                :z-index="overlayZIndex"
                :style="{left:'0',right:'0'}"
                @clean-click="dismiss">

        </drag-handle>
        <ul :id="id" ref="nav"
            @click="onClick"
            @keyup.esc="dismiss"
            :style="computedStyle"
            :class="computedClass">
            <slot></slot>
        </ul>
    </div>
</template>


<script>
    // https://github.com/vue-comps/vue-side-nav

    import createOverlay from '../vendor/overlay'

    export default {

        components: {
            "drag-handle": require("./drag-handle.vue")
        },

        created() {
            return this.overlay = createOverlay(this.Vue);
        },

        mixins:[
            require("vue-mixins/vue"),
            require("vue-mixins/onWindowResize"),
            require("vue-mixins/setCss"),
            require("vue-mixins/isOpened2"),
            require("vue-mixins/class"),
            require("vue-mixins/style")
        ],

        props: {
            id             : String,
            class          : {
                default() {
                    return ["side-nav"];
                }
            },
            style          : {
                default() {
                    return [];
                }
            },
            width          : {type: Number, coerce: Number, default: 240},
            opacity        : {type: Number, default: 0.5, coerce: Number},
            right          : {type: Boolean, default: false},
            notDismissable : {type: Boolean, default: false},
            closeOnClick   : {type: Boolean, default: false},
            fixed          : {type: Boolean, default: false},
            fixedScreenSize: {type: Number, coerce: Number, default: 992},
            transition     : {
                type: Function,
                default({el, style, cb}) {
                    this.position = style[this.side].replace("px", "");
                    return cb();
                }
            },
            zIndex         : {type: Number, default: 1000, coerce: Number}
        },

        computed: {
            side() { if (this.right) { return "right"; } else { return "left"; } },
            otherSide() { if (this.right) { return "left"; } else { return "right"; } },
            mergeClass() { return {fixed: this.fixed}; },
            mergeStyle() {
                let style = {
                    position: "fixed",
                    width: this.width + "px",
                    top: "0",
                    margin: "0",
                    height: "100%",
                    zIndex: this.overlayZIndex+1,
                    boxSizing:"border-box",
                    transform:"translateX(0)"
                };
                style[this.otherSide] = 'initial';

                style[this.side] = this.position + "px";
                return style;
            },
            realWidth() {
                let width;
                if (this.computedStyle[1] != null) {
                    ({ width } = this.computedStyle[1]);
                }
                if (width == null) { ({ width } = this.computedStyle[0]); }
                return width;
            }
        },
        watch: {
            fixed: "processFixed",
            fixedScreenSize: "processFixed",
            side: "setParentMargin"
        },
        data() {
            return {
                isFixed: null,
                position: -1 * (this.width + 10),
                overlayZIndex: 1001
            };
        },
        methods: {
            makeFixed(fixed) {
                if (fixed !== this.isFixed) {
                    this.isFixed = fixed;
                    this.setParentMargin();
                    return this.$emit("fixed", fixed);
                }
            },
            setParentMargin() {
                if (this.$el.parentElement) {
                    let width;
                    if (this.isFixed) {
                        width = this.realWidth;
                    } else {
                        width = null;
                    }
                    return (() => {
                        let result = [];
                        for (let el of this.$el.parentElement.children) {
                            let item;
                            if (el !== this.$el) {
                                this.setCss(el,`margin-${this.side}`,width);
                                item = this.setCss(el,`margin-${this.otherSide}`,null);
                            }
                            result.push(item);
                        }
                        return result;
                    })();
                }
            },
            processFixed() {
                if (this.fixed) {
                    this.makeFixed(window.innerWidth > this.fixedScreenSize);
                    if (this.isFixed) {
                        this.position = 0;
                    } else {
                        this.position = -1 * (this.width + 10);
                    }
                    this.disposeWindowResize = this.onWindowResize(() => {
                            if (window.innerWidth > this.fixedScreenSize) { // getting bigger
                                if (!this.isFixed) {
                                    if (this.opened) {
                                        this.close(true);
                                        this.wasOpened = true;
                                    } else {
                                        this.show(false);
                                    }
                                    return this.makeFixed(true);
                                }
                            } else { // getting smaller
                                if (this.isFixed) {
                                    if (this.wasOpened) {
                                        this.open(true);
                                    } else {
                                        this.hide(false);
                                    }
                                    return this.makeFixed(false);
                                }
                            }
                        }
                    );
                } else {
                    this.makeFixed(false);
                    if (this.opened) {
                        this.position = 0;
                    } else {
                        this.position = -1 * (this.width + 10);
                    }
                    __guardMethod__(this, 'disposeWindowResize', o => o.disposeWindowResize());
                }
                return this.setParentMargin();
            },
            onClick(e) {
                if (this.closeOnClick) {
                    return this.dismiss(e);
                }
            },
            dismiss(e) {
                if (!e.defaultPrevented) {
                    if (!this.notDismissable && !this.isFixed) {
                        this.close();
                        return e.preventDefault();
                    }
                }
            },

            move(position) {
                let fac = this.right ? -1 : 1;
                return this.position = -this.width+(fac*position);
            },

            show(animate) {
                if (animate == null) { animate = true; }
                this.$emit("before-enter");
                if (animate) {
                    let style = {};
                    style[this.side] = "0";
                    return this.transition({el:this.$refs.nav, style, cb: () => {
                        this.setCss(this.$refs.nav, "transform", "translateX(0)");
                        this.setOpened();
                        return this.$emit("after-enter");
                    }
                    });
                } else {
                    this.position = 0;
                    this.setOpened();
                    return this.$emit("after-enter");
                }
            },

            hide(animate) {
                if (animate == null) { animate = true; }
                this.wasOpened = false;
                this.$emit("before-leave");
                if (animate) {
                    let style = {};
                    style[this.side] = (-1 * (this.width + 10)) + "px";
                    return this.transition({el:this.$refs.nav, style, cb: () => {
                        this.setClosed();
                        return this.$emit("after-leave");
                    }
                    });
                } else {
                    this.position = -1 * (this.width + 10);
                    this.setClosed();
                    return this.$emit("after-leave");
                }
            },

            open(restoreOverlay) {
                if (this.opened && !restoreOverlay) { return; }
                let {zIndex,close} = this.overlay.open({zIndex:this.zIndex, opacity:this.opacity, onBeforeClose: () => this.close()});
                this.overlayZIndex = zIndex;
                this.closeOverlay = close;
                if (!restoreOverlay) {
                    return this.show();
                }
            },

            close(restoreNav) {
                if (!this.opened) { return; }
                __guardMethod__(this, 'closeOverlay', o => o.closeOverlay(false));
                this.closeOverlay = null;
                if (!restoreNav) {
                    return this.hide();
                }
            },

            toggle() {
                if (this.isFixed) { // disable opening
                    return this.opened = this.isOpened;
                } else {
                    if (this.opened) {
                        return this.close();
                    } else {
                        return this.open();
                    }
                }
            }
        },

        mounted() { return this.$nextTick(function() {
            return this.processFixed();
        }); },

        beforeDestory() {
            return __guardMethod__(this, 'closeOverlay', o => o.closeOverlay());
        }
    };
    function __guardMethod__(obj, methodName, transform) {
        if (typeof obj !== 'undefined' && obj !== null && typeof obj[methodName] === 'function') {
            return transform(obj, methodName);
        } else {
            return undefined;
        }
    }
</script>


