<template>

    <footer class="c-footer">
        <slot></slot>
        <slot name="copyright" v-if="hideCopyright !== true">
            <div class="copyright">
                <div class="container">
                    <div class="row">
                        <slot name="left">
                            <p class="left">Copyright 2017 - Codex Project</p>
                        </slot>
                        <slot name="right">
                            <p class="right">Brought to you by Codex - a Open Source documentation platform</p>
                        </slot>
                    </div>
                </div>
            </div>
        </slot>
    </footer>
</template>


<script>
    export default {
        name : 'c-footer',
        props: {
            hideCopyright: Boolean,
            textLeft     : {type: String, default: 'Copyright 2017 - Codex Project'},
            textRight    : {type: String, default: 'Brought to you by Codex - a Open Source documentation platform'}
        }
    }
</script>

<style scoped>
    .copyright:after {
        clear: both;
    }
    .left {
        float: left;
    }

    .right {
        float: right;
    }
</style>


