<template>
    <li class="nav-item">
        <a href="javascript:void(0)" class="nav-link" :class="{ 'nav-link-split': title, 'nav-link-icon': icon }">
            <div class="nav-link-top" v-if="title">{{ title }}</div>
            <span v-if="label">{{ label }}</span>
            <i v-if="icon" :class="'fa fa-' + icon"></i>
            <slot></slot>
        </a>
    </li>
</template>

<script>
    import {getRandomId} from '@radic/util'
    //    import VBtn from 'bootstrap-vue/components/menu-item.vue'
    export default {
        name : 'c-header-menu-item',
        props: {
            label: {type: [Boolean, String], default: false},
            icon : {type: [Boolean, String], default: false},
            title: {type: [Boolean, String], default: false},
        },
        data(){
            return {
                id: getRandomId(5)
            }
        },
        watch: {
            $route(){
                $('#' + this.id).sideNav('hide')
            }
        }
    }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
    @import '../styles/base';

    .nav-item {
        float: left;
        margin: 0 0 0 1px;
    }

    .nav-item > .nav-link {
        display: block;
        //color: $header-item-link-color;
        //background: $header-item-bg-color; //lighten($header-bg-color, 20);
        height: $header-height;
        font-weight: 300;
        padding: 15px 20px;
        text-align: center;

        &.nav-link-icon {
        }

        &.nav-link-split {
            position: relative;
            /*padding-top: 20px;*/
            text-transform: none;

            .nav-link-top {
                height: $header-height / 3;
                width: 100%;
                display: block;
                position: absolute;
                //color: $header-item-split-top-color;
                //background: $header-item-split-top-bg-color;
                top: 0;
                left: 0;
                line-height: 16px;
                padding: 0 5px;
                font-size: 11px;
                text-transform: lowercase;
                text-align: left;
            }
        }

    }

    @include media-breakpoint-up(md) {

        .nav-link {
            margin: 0;
        }

    }

</style>


