<template lang="html">
    <div context-menu
         @click.stop
         @contextmenu.stop
         :id="id"
         class="context-menu-menu-container"
         :style="ctxStyle">
        <div style="background-color:transparent" class="ctx open">
            <slot name="menu">
                <ul
                        role="menu"
                        class="context-menu-menu dropdown-menu"
                        :class="{
                          'ctx-menu-right': align==='right',
                          'ctx-menu-left': align==='left'
                        }"
                >
                    <slot></slot>
                </ul>
            </slot>
        </div>
    </div>
</template>
<script type="text/babel">
    import {createBodyClickListener} from 'codex-core/utils'

    // const EVENT_LIST = ['click', 'contextmenu', 'keydown']

    export default {
        name    : 'context-menu',
        props   : {
            id: {
                type   : String,
                default: 'default-ctx'
            },
            align: {type:String, default:'left'}
        },
        data() {
            return {
                locals           : {},
                ctxTop           : 0,
                ctxLeft          : 0,
                ctxVisible       : false,

                bodyClickListener: createBodyClickListener(
                    (e) => {
                        let isOpen = ! ! this.ctxVisible
                        let outsideClick = isOpen && ! this.$el.contains(e.target)

                        if ( outsideClick ) {
                            if ( e.which !== 1 ) {
                                e.preventDefault()
                                e.stopPropagation()
                                return false;
                            } else {
                                this.close()
                                e.stopPropagation()
                            }
                        } else {
//                            this.ctxVisible = false
//                            this.$emit('ctx-close', this.locals)
                        }
                    }
                )
            }
        },
        methods : {

            setPositionFromEvent(e) {
                const {pageX, pageY} = e
                this.ctxTop = pageY - (document.body.scrollTop)
                this.ctxLeft = pageX
            },

            open(e, data) {
                if ( this.ctxVisible ) this.ctxVisible = false
                this.ctxVisible = true
                this.$emit('context-menu-open', this.locals = data || {})
                this.setPositionFromEvent(e)
                this.$el.setAttribute('tab-index', - 1)
                this.bodyClickListener.start()
                return this
            },

            close(){

                this.ctxVisible = false
                this.$emit('context-menu-cancel', this.locals)
            }
        },
        watch   : {
            ctxVisible(newVal, oldVal) {
                if ( oldVal === true && newVal === false ) {
                    this.bodyClickListener.stop((e) => {
                        //
                        // this.locals = {}
                    })
                }
            }
        },
        computed: {
            ctxStyle() {
                return {
                    'display': this.ctxVisible ? 'block' : 'none',
                    'top'    : (this.ctxTop || 0) + 'px',
                    'left'   : (this.ctxLeft || 0) + 'px'
                }
            }
        }
    }
</script>

<style lang="scss" rel="stylesheet/scss">
    .context-menu {
        position: relative;
    }

    .context-menu-menu {
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 1000;
        display: none;
        float: left;
        min-width: 160px;
        padding: 5px 0;
        margin: 2px 0 0;
        font-size: .9rem;
        color: #373a3c;
        text-align: left;
        list-style: none;
        background-color: #ffffff;
        -webkit-background-clip: padding-box;
        background-clip: padding-box;
        border: 1px solid rgba(0, 0, 0, .15);
        border-radius: .25rem;
    }

    .context-menu-divider {
        height: 1px;
        margin: .5rem 0;
        overflow: hidden;
        background-color: #e5e5e5;
    }

    .context-menu-item {
        display: block;
        /*width: 100%;*/
        padding: 3px 20px;
        clear: both;
        font-weight: normal;
        line-height: 1.5;
        color: #373a3c;
        text-align: inherit;
        white-space: nowrap;
        background: none;
        border: 0;
        cursor: default;
    }

    .context-menu-item:focus,
    .context-menu-item:hover {
        color: #2b2d2f;
        text-decoration: none;
        background-color: #f5f5f5;
        cursor: normal;
    }

    .context-menu-item.active,
    .context-menu-item.active:focus,
    .context-menu-item.active:hover {
        color: #ffffff;
        text-decoration: none;
        background-color: #0275d8;
        outline: 0;
    }

    .context-menu-item.disabled,
    .context-menu-item.disabled:focus,
    .context-menu-item.disabled:hover {
        color: #818a91;
    }

    .context-menu-item.disabled:focus,
    .context-menu-item.disabled:hover {
        text-decoration: none;
        cursor: not-allowed;
        background-color: transparent;
        background-image: none;
        filter: "progid:DXImageTransform.Microsoft.gradient(enabled = false)";
    }

    .open > .context-menu-menu {
        display: block;
    }

    .open > a {
        outline: 0;
    }

    .context-menu-menu-right {
        right: 0;
        left: auto;
    }

    .context-menu-menu-left {
        right: auto;
        left: 0;
    }

    .context-menu-header {
        display: block;
        padding: 3px 20px;
        font-size: .9rem;
        line-height: 1.5;
        color: #818a91;
        white-space: nowrap;
    }

    .context-menu-backdrop {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 990;
    }

    .pull-right > .context-menu-menu {
        right: 0;
        left: auto;
    }

    .context-menu-menu-container {
        position: fixed;
        padding: 0;
        border: 1px solid #bbbbbb;
        background-color: whitesmoke;
        z-index: 99999;
        box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
    }
</style>


