<template>
    <li :class="{ 'open': open, 'active': active }">

        <a v-if="hasChildren !== true" :href="header ? false : href" :class="header ? ['subheader'] : []">
            <i v-if="iconClass" :class="['fa', iconClass]"></i>
            <slot>{{ title }}</slot>
        </a>

        <a v-if="hasChildren === true" @click="toggleSubMenu()">
            <i v-if="iconClass" :class="['fa', iconClass]"></i>
            <slot name="title">{{ title }}</slot>
            <span class="arrow"></span>
        </a>
        <transition @enter="slideEnter" v-if="hasChildren" @leave="slideLeave" :css="false">
            <ul ref="ul" v-if="hasChildren === true" class="sub-menu" v-show="open">
                <slot></slot>
            </ul>
        </transition>
    </li>
</template>

<script>
    import {cSlide} from '../transitions';
    export default {
        name   : 'c-side-nav-item',
        props  : {
            hasChildren: {type: Boolean, default: false},
            header     : Boolean,
            title      : String,
            href       : String,
            icon: String
        },
        data(){
            return {
                open: false,
                active: false
            }
        },
        mounted(){

        },
        computed:{
            iconClass(){
                if(this.header) return false;
                if(this.icon) return 'fa-' + this.icon;
                return this.hasChildren ? 'fa-folder' : 'fa-book';
            }
        },
        methods: {
            ...cSlide(150),
            openSubMenu(){
                this.open = true;
            },
            closeSubMenu(){
                this.open = false;
            },
            toggleSubMenu(){
                if ( this.open )
                    this.closeSubMenu();
                else
                    this.openSubMenu();
            },
        }
    }
</script>




