<template>
    <header class="c-header">
        <div class="header-inner">
            <div class="logo" :class="logoClass" v-if="showLogo">
                <slot name="logo">
                    <h1 @click="handleLogoClick">{{ logoText }}</h1>
                    <span class="logo-beta-text">Beta</span>
                </slot>
                <div class="sidebar-toggle-desktop" v-if="showToggle && md" @click="toggleSidebar"></div>
            </div>
            <a href="#" class="sidebar-toggle-mobile" v-if="showToggle" @click="toggleSidebar"></a>


            <div class="header-menu">

                <ui-button v-if="isDropdown" type="secondary" has-dropdown size="large" @dropdown-open="dropdownOpen">
                    <span>MENU</span>
                    <nav ref="dropdown" class="nav nav-header nav-header-dropdown" slot="dropdown">
                        <slot name="menu"></slot>
                    </nav>
                </ui-button>

                <ul class="nav nav-header nav-header-horizontal" v-if="! isDropdown">
                    <slot name="menu"></slot>
                </ul>

            </div>


        </div>
        <slot name="after"></slot>
        <!--<c-side-nav ref="sidenav-header-menu" :draggable="false" v-if="md === false">-->
        <!--<li><a class="subheader">Menu</a></li>-->
        <!--<slot name="menu"></slot>-->
        <!--</c-side-nav>-->
    </header>

    <!--<c-header-button v-on:click.native="toggleSidenav('projects')" title="Project" :label="project.display_name"></c-header-button>-->

</template>
<style lang="scss" rel="stylesheet/scss">

    @import '../styles/base';

    .ui-popover.ui-popover-header-dropdown {
        background: none;
    }

    .nav.nav-header {
        .nav-link {
            @include button-size($btn-padding-y, $btn-padding-x, 0.9rem, $btn-border-radius);
            @include transition(all .2s ease-in-out);

            &,
            &:active,
            &.active {
                &:focus,
                &.focus {
                    @include tab-focus();
                }
            }

            @include hover-focus {
                text-decoration: none;
            }
            &.focus {
                text-decoration: none;
            }

            &:active,
            &.active {
                background-image: none;
                outline: 0;
                @include box-shadow($btn-active-box-shadow);
            }

            &.disabled,
            &:disabled {
                cursor: $cursor-disabled;
                opacity: .65;
                @include box-shadow(none);
            }
            background: none;
            border: none;
            box-shadow: none;
            text-transform: uppercase;
            font-size: 0.9rem;
            height: $header-height;
            font-weight: 300;
            margin-left: 0 !important;
            background: rgba(120, 144, 156, 0.28);
            color: #eceff1;
            line-height: 35px;
            text-align: center;
            white-space: nowrap;
            vertical-align: middle;
            cursor: pointer;
            user-select: none;

            &:hover, &:focus {
                box-shadow: none;
            }

            &.active {
                color: $color-codex;
                //border-bottom: 2px solid $color-codex;
            }
        }

        &-dropdown {
            max-width: rem-calc(360px);

            .nav-link {
                display: block;
                background: rgba(67, 83, 91, 0.88);
            }
        }

        &-horizontal {
            .nav-link {
                display: inline-block;
            }
        }
    }
</style>
<style lang="scss" rel="stylesheet/scss" scoped>
    @import '../styles/base';

    .header-menu {
        .ui-button {
            height: $header-height;
            background: rgba(120, 144, 156, 0.28);
            color: #eceff1;
        }
    }

    .c-header {
        height: $header-height;
        min-height: $header-height;
        width: 100%;

        .header-fixed & {
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
        }

        .header-inner, .header-menu, .nav-header, .nav-header .nav-item {
            height: inherit;
        }

        //margin: 0;
        //padding: 0;
        //border: 0;

        .logo {
            display: block;
            height: $header-height;
            width: auto;
            margin-left: $page-gutter-size;
            float: left;

            &.logo-text {
                padding: $header-logo-text-padding;

                > h1 {
                    font-size: $header-logo-text-font-size;
                    color: $header-logo-text-color;
                    margin: 0;
                    cursor: pointer;
                }
            }
        }

        .logo-beta-text {
            color: #b0bec5;
            font-size: 12px;
            text-transform: uppercase;
            position: absolute;
            top: 34px;
            left: 75px;
        }

        // @TODO: Replace with proper icon that is color-able
        .sidebar-toggle-desktop, .sidebar-toggle-mobile {
            margin: 7px 7px;
            width: 24px;
            height: 20px;
            background-size: 87%;
            // http://www.flaticon.com/free-icon/menu_149187
            //background-image: url(data:image/svg+xml;utf8;base64,);
            cursor: pointer;

            .sidebar-hidden & {
                display: none;
            }
        }

        .sidebar-toggle-mobile {
            float: right;
            margin: 15px 20px 0 15px;
        }

        .sidebar-toggle-desktop { display: none }

        .header-menu {
            display: block;
            clear: none;
            float: right;
        }

    }

    ////////////////////////////////
    //       Desktop
    ////////////////////////////////
    @include media-breakpoint-up(md) {
        .c-header {
            //padding: 0 $page-gutter-size;

            .logo {
                display: inline-block;
                width: $sidebar-width;
                > h1 {
                    display: inline-block;
                }
            }

            .logo-beta-text {
                position: static;
            }
            .sidebar-toggle-mobile { display: none }
            .sidebar-toggle-desktop { display: block; float: right; }

            .header-menu {
                padding: 0;
                float: right;
                clear: none;
            }
        }
    }


</style>

<script>
    import {layout} from '../mixins'
    import {mapActions} from 'vuex'
    export default {
        name  : 'c-header',
        mixins: [layout],
        props : {
            logoHref  : {type: String, default: '/'},
            logoText  : {type: String, default: 'Codex'},
            logoClass : {type: String, default: 'logo-text'},
            showLogo  : {type: Boolean, default: true},
            showToggle: {type: Boolean, default: false},
            isDropdown: Boolean,
        },


        data() {
            return {md: false}; //, logoLink: '', logoText: '', showLogo: true, showToggle: false}
        },
        mounted(){
            this.handleResize();
            this.$on('resize', this.handleResize);
            this.$$ready(() => {
                this.handleResize();
                if ( this.$refs['sidenav-header-menu'] ) {
                    document.body.appendChild(this.$refs['sidenav-header-menu'].$el)
                }
            })
        },
        beforeDestroy(){
            this.$off('resize', this.handleResize);
        },
        methods: {
            ...mapActions(['toggleSidebar']),
            handleResize(){
                this.md = this.isBreakpointUp('md');
            },
            toggleHeaderMenu(){

                this.$refs['sidenav-header-menu'].toggle()
            },
            handleLogoClick(){
                window.document.location.replace(this.logoHref)
            },
            dropdownOpen(){
                let popover = this.$refs.dropdown.parentNode;
                let dropContent = popover.parentNode;
                let dropEl = dropContent.parentNode;

                if ( ! popover.classList.contains('ui-popover-header-dropdown') ) {
                    popover.classList.add('ui-popover-header-dropdown');
                }
                console.log('dropdownOpen', {self: this, popover, dropContent, dropEl})
                let display = dropEl.style.display;
                dropEl.style.display = 'none';
                this.$nextTick(() => {
                    dropEl.style.display = display;
                    dropEl.style.position = 'fixed';
                })
            }
        }
    }
</script>



