// out: ..
<template>
    <div style="opacity:0;position:fixed;top:-10px;left:0;right:0;height:120vh;willChange:opacity" :style="{zIndex:zIndex,backgroundColor:color}" @click="dismiss"></div>
</template>


<script>

    export default {

        mixins: [
            require("vue-mixins/setCss"),
            require("vue-mixins/onDocument"),
            require("vue-mixins/getViewportSize")
        ],

        computed: {
            zIndex() {
                if ( this.lastItem != null ) {
                    return this.lastItem.zIndex;
                }
                return 995;
            },
            color() {
                if ( (this.lastItem != null) && this.lastItem.color ) {
                    return this.lastItem.color;
                }
                return "black";
            },
            opacity() {
                if ( this.lastItem != null ) {
                    if ( this.lastItem.opacity != null ) {
                        return this.lastItem.opacity;
                    }
                    return 0.5;
                }
                return 0;
            },
            dismissable() {
                if ( (this.lastItem != null) && (this.lastItem.dismissable != null) ) {
                    return this.lastItem.dismissable;
                }
                return true;
            },
            lastItem() {
                if ( this.stack.length > 0 ) {
                    let li = this.stack[this.stack.length - 1];
                    this.updateScroll(li);
                    this.updateKeyListener(true);
                    return li;
                }
                this.updateScroll();
                this.updateKeyListener();
                return null;
            }
        },

        data() {
            return {stack: []};
        },

        methods: {

            fade({el, opacity, cb}) {
                this.setCss(el, "opacity", opacity);
                return cb();
            },

            dismiss(e) {
                if ( (e != null) && ! e.defaultPrevented ) {
                    if ( this.dismissable ) {
                        if ( (e.type === "keyup") && (e.which !== 27) ) {
                            return null;
                        }
                        this.close();
                        return e.preventDefault();
                    }
                }
            },

            updateKeyListener(set) {
                if ( set && ! this.removeListener ) {
                    if ( ! this.removeListener ) {
                        return this.removeListener = this.onDocument("keyup", this.dismiss);
                    }
                } else {
                    if ( typeof this.removeListener === 'function' ) {
                        this.removeListener();
                    }
                    return this.removeListener = null;
                }
            },


            updateScroll(options) {
                let style = {o: null, m: null};
                if ( options && ! options.allowScroll ) {
                    if ( this.scrollDisabled ) {
                        return null;
                    }
                    style.o = "hidden";
                    style.m = (this.getViewportSize().width - document.documentElement.clientWidth) + "px";
                    this.scrollDisabled = true;
                } else {
                    this.scrollDisabled = false;
                }
                this.setCss(document.documentElement, "overflow", style.o);
                return this.setCss(document.documentElement, "margin-right", style.m);
            },

            open(options) {
                if ( options == null ) {
                    options = {};
                }
                if ( this.lastItem == null ) {
                    document.body.appendChild(this.$el);
                }
                if ( typeof options.onBeforeOpen === 'function' ) {
                    options.onBeforeOpen();
                }
                let newZIndex = this.zIndex + 5;
                if ( (options.zIndex == null) || (options.zIndex <= newZIndex) ) {
                    options.zIndex = newZIndex;
                }
                this.stack.push(options);
                this.fade({
                    el: this.$el, opacity: this.opacity, cb() {
                        return (typeof options.onOpened === 'function' ? options.onOpened() : undefined);
                    }
                });
                return {zIndex: this.zIndex + 1, close: callCbs => (callCbs != null ? callCbs : (callCbs = true), this.close(options, callCbs))};
            },

            close(options, callCbs) {
                let index;
                if ( options == null ) {
                    options = this.lastItem;
                }
                if ( callCbs == null ) {
                    callCbs = true;
                }
                if ( (index = this.stack.indexOf(options)) > - 1 ) {
                    this.stack.splice(index, 1);
                    if ( callCbs ) {
                        if ( typeof options.onBeforeClose === 'function' ) {
                            options.onBeforeClose();
                        }
                    }
                    return this.fade({
                        el: this.$el, opacity: this.opacity, cb: () => {
                            if ( callCbs ) {
                                if ( typeof options.onClosed === 'function' ) {
                                    options.onClosed();
                                }
                            }
                            if ( this.lastItem == null ) {
                                return document.body.removeChild(this.$el);
                            }
                        }
                    });
                }
            }
        }
    };

</script>



